/* FONT FAMILY*/
$font-family: 'Montserrat', sans-serif;

/* FONT WEIGHT */
$font-weight-light: 300;
$font-weight-regular: 400;
$font-weight-medium: 500;
$font-weight-semibold: 600;
$font-weight-bold: 700;

/*FONT SIZE*/
$font-size-s: 0.5625rem;
$font-size-m: 0.75rem;
$font-size-b: 0.9375rem;
$$font-size-l: 1.125rem;

/*LINE HEIGHT*/
$line-height-l: 1.3;
$line-height-b: 1.3;
$line-height-m: 1.3;
$line-height-s: 1.3;


 .font-b-b, .font-b-s,.font-m-r, .font-m-ru, .font-m-m, .font-m-l, .font-s-ru, .font-s-l, .font-s-li {
    font-family: $font-family;
} 

.font-b-b, .font-b-s{
    font-size: $font-size-b;
    line-height: $line-height-b;
}

.font-b-b {
    font-weight: $font-weight-bold;
}

.font-b-s {
    font-weight: $font-weight-semibold;
}

.font-b-mic {
    font-weight: $font-weight-medium;
    text-transform: capitalize;
    font-style: italic;
}

.font-m-ru, .font-m-m, .font-m-l {
    font-size: $font-size-m;
    line-height: $line-height-m;
}

.font-m-r {
    font-size: $font-size-m;
    line-height: $line-height-l;
    font-weight: $font-weight-light;
    text-transform: uppercase;
}

.font-m-ru {
    font-weight: $font-weight-regular;
    text-transform: uppercase;
}

.font-m-m {
    font-weight: $font-weight-medium;
}

.font-m-mu {
    font-weight: $font-weight-medium;
    text-transform: uppercase;
}

.font-m-s {
    font-weight: $font-weight-semibold;
}

.font-m-b {
    font-weight: $font-weight-bold;
}

.font-m-bu {
    font-weight: $font-weight-bold;
    text-transform: uppercase;
}


.font-m-l {
    font-weight: $font-weight-light;
}

.font-s-ru, .font-s-m, .font-s-l, .font-s-li {
    font-size: $font-size-s;
}

.font-s-ru {
    font-weight: $font-weight-regular;
    text-transform: uppercase;
}

.font-s-m {
    font-weight: $font-weight-medium;
}

.font-s-l {
    font-weight: 300;
}

.font-s-li {
    font-weight: 300;
    font-style: italic;
}

